.statistics {
  width: 400px;
  height: 150px;
  background-color: #fff;
  margin: 50px auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.5);
}

.title {
  display: inline-block;
  text-transform: uppercase;
  padding-top: 30px;
  color: rgb(36, 36, 36);
}
